@import "../../../../styles/variables";

.post {
  & + & {
    margin: 10px 0 0 0;
  }

  &__header {
    display: flex;
    margin: 0 0 10px 0;
  }


  &__name {
    margin: 0 0 3px 0;
    color: $text-link;
    font-weight: 700;
  }

  &__date {
    color: $text-color-secondary;
    font-size: 0.9rem;
  }

  &__text {
    margin: 0 0 15px 0;
    color: #000;
  }

}