.user__list{display:grid;grid-template-columns:160px 160px 160px;grid-column-gap:15px;grid-row-gap:20px;justify-content:center;padding:15px 0 0 0}.user__avatar{position:relative;display:block;height:160px;overflow:hidden;border-radius:4px}.user__img{display:block;object-fit:cover;object-position:center;width:100%}.user__info{display:flex;justify-content:space-between;align-items:center;margin-top:5px;padding:0 5px 0 0}.user__name{font-size:1rem;color:#2a5885;font-weight:700}.user__add{position:relative;cursor:pointer;background:transparent}.user__add:hover .user__icon,.user__add:hover:before,.user__add:hover:after{opacity:1}.user__add:after{content:"Добавить в друзья";opacity:0;position:absolute;bottom:calc(100% + 10px);left:50%;padding:4px 5px;white-space:nowrap;background:rgba(0,0,0,.6);color:#fff;border-radius:4px;transform:translateX(-20%);z-index:10;transition:opacity .3s ease}.user__add:before{content:"";opacity:0;position:absolute;top:-10px;left:50%;border:5px solid transparent;border-top:5px solid rgba(0,0,0,.6);transform:translateX(-66%);transition:opacity .3s ease}.user__icon{font-size:20px;color:#2a5885;opacity:.65;transition:opacity .2s ease;background:transparent}.user__button{width:20px;height:20px}